l(/src/assets/fonts/MonsieurLaDoulaise-Regular.ttf);\n}\n\n@font-face {\n    font-family: \"Subheader-Main\";\n    src: url(/src/assets/fonts/Roboto-Thin.ttf);\n}\n\nh1 {\n    font-family: \"Header-Main\";\n}\n\nh2, h3, h4, h5, h6 {\n    font-family: \"Subheader-Main\";\n}\n\np, a, button {\n    font-family: \"Body-Main\";\n}\n\n::placeholder {\n    font-family: \"Body-Main\";\n}\n\n/* Header */\n\n.header {\n    background-image: url(\"/src/assets/images/header.jpg\");\n    background-repeat: no-repeat;\n    background-size: 100%;\n    background-position: 100%;\n    background-color: rgb(0, 0, 0);\n    color: white;\n    height: 300px;\n    width: 100%;\n    display: flex;\n    flex-direction: column;\n}\n\n.header__top {\n    font-family: \"Header-Main\";\n    font-size: 2rem;\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n}\n\n.logo {\n    cursor: pointer;\n    border-bottom: 2px solid red;\n}\n\n.header__top, \n.header__bottom {\n    padding: 0 35px 0 35px;\n}\n\n.header__bottom {\n    width: 80%;\n}\n\n.header__bottom > p {\n    margin: 0;\n    color: rgb(255, 27, 27);\n    font-size: 1.5rem;\n}\n\n/* Body Cards */\n\n.body-card {\n    background-color: black;\n    color: white;\n    height: 200px;\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: flex-start;\n    /* border-bottom: 1px solid white; */\n    padding: 0 40px 0 40px;\n}\n\n.cardBtn {\n    background-color: #B5A080;\n    color: white;\n    border: none;\n    width: 140px;\n    height: 40px;\n    transition: background-color 1s;\n}\n\n.cardBtn:hover {\n    background-color: white;\n    color: #B5A080;\n    cursor: pointer;\n    box-shadow: inset 0 -40px 0 -1px white;\n}\n\n.body-card > h3 {\n    font-size: 1.5rem;\n    margin-bottom: 10px;\n}\n\n.card__one > p {\n    font-size: 1rem;\n    margin-bottom: 30px;\n}\n\n.card__two > p {\n    font-size: 1rem;\n    margin-bottom: 30px;\n}\n\n.card__three {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n}\n\n.card__four {\n    display: flex;\n    flex-direction: column;\n    height: 300px;\n    padding: 20rem 20px 20rem 20px;\n}\n\n.card__four__top > h3 {\n    font-size: 1.5rem;\n}\n\n.card__four__top > p {\n    margin-bottom: 30px;\n    font-size: 1rem;\n    border-bottom: 1px solid white;\n    margin-bottom: 4rem;\n}\n\n.card__four__top {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    margin: 0 auto;\n}\n\n.card__four__bottom {\n    display: grid;\n    grid-gap: 30px;\n    grid-template-columns: 1fr 1fr;\n    width: 90%;\n    margin: 0 auto;\n}\n\n.img-div {\n    height: 200px;\n    width: 250px;\n    background-color: #B5A080;\n    margin: 0 auto;\n    cursor: pointer;\n    background-size: 150%;\n    background-repeat: no-repeat;\n}\n\n#social-img1 {\n    background-image: url(/src/assets/images/img1.jpg);\n}\n\n#social-img2 {\n    background-image: url(/src/assets/images/img2.jpg);\n}\n\n#social-img3 {\n    background-image: url(/src/assets/images/img3.jpg);\n}\n\n#social-img4 {\n    background-image: url(/src/assets/images/img4.jpeg);\n}\n\n#social-img5 {\n    background-image: url(/src/assets/images/img5.jpg);\n}\n\n#social-img6 {\n    background-image: url(/src/assets/images/img6.jpg);\n}\n\n\n/* Footer */\n.footer {\n    background-color: rgb(71, 71, 71);\n    color: white;\n    display: grid;\n    min-height: 350px;\n    grid-template-rows: 60% 40%;\n    grid-template-columns: 50% 50%;\n    flex-direction: column;\n}\n\n.footer__contact > h3, \n.footer__dresscode > h3, \n.footer__subscribe > h3 {\n    margin: 20px 0 10px 0;\n    border-bottom: 1px solid red;\n}\n\n.footer__contact, \n.footer__dresscode, \n.footer__subscribe {\n    display: flex;\n    flex-direction: column;\n    justify-content: flex-start;\n    align-items: flex-start;\n}\n\n.footer__contact {\n    grid-row: 1;\n    grid-column: 1;\n    padding: 0 40px 0 40px;\n}\n\n.footer__dresscode {\n    grid-row: 2;\n    grid-column: 1;\n    padding: 0 40px 0 40px;\n    margin-top: 15px;\n}\n\n.footer__subscribe {\n    grid-row: span 2;\n    grid-column: 2;\n}\n\n.footer__contact > p, \n.footer__dresscode > p, \n.footer__subscribe > p {\n    margin: 10px 0 10px 0;\n    font-size: 0.7rem\n}\n\n.footer__input-div > input {\n    width: 185px;\n    height: 30px;\n    padding: 10px;\n    margin: 20px 10px 20px 0;\n}\n\n.footer__input-div > input:focus {\n    outline: none;\n}\n\n.footer__input-div > button {\n    background-color: #B5A080;\n    border: none;\n    width: 100px;\n    height: 55px;\n}\n\n.footer__input-div > button:hover {\n    background-color: white;\n    cursor: pointer;\n    transition: background-color 0.3s;\n}\n\n.footer__copyright {\n    background-color: black;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    grid-row: 3;\n    grid-column: span 2;\n    font-size: 0.7rem\n}\n\n/* --- Reservations Page --- */\n\n#calendar {\n    margin: 0px 0px;\n    border-radius: 0;\n}\n\n.confirmation-message {\n    color: rgb(141, 176, 108);\n    text-align: center;\n}\n\n/* --- Lounge Page --- */\n#lounge-card {\n    display: flex;\n    justify-content: flex-start;\n    align-items: center;\n    padding-top: 30px;\n    background-image: url(\"/src/assets/images/lounge.jpg\");\n    background-size: 100%;\n    background-position: 20%;\n    background-repeat: no-repeat;\n}\n\n#lounge-card > h3 {\n    font-size: 1.8rem;\n}\n\n#lounge-card-two {\n    display: grid;\n    grid-template-rows: 1fr 1fr 1fr 1fr;\n    grid-template-columns: 1fr 1fr;\n    color: white;\n    padding-top: 2rem;\n    padding-bottom: 16rem;\n    gap: 20px;\n    overflow: hidden;\n}\n\n#lounge-card-two > h4 {\n    grid-row: 1;\n    grid-column: span 2;\n    text-align: center;\n    font-size: 1.8rem;\n}\n\n.dj-div {\n    text-align: center;\n}\n\n.dj-name {\n    color: #B5A080;\n}\n\n.dj-one {\n    grid-row: 2;\n    grid-column: 1;\n}\n\n.dj-two {\n    grid-row: 2;\n    grid-column: 2;\n}\n\n.dj-three {\n    grid-row: 3;\n    grid-column: 1;\n}\n\n.dj-four {\n    grid-row: 3;\n    grid-column: 2;\n}\n\n.dj-five {\n    grid-row: 4;\n    grid-column: 1;\n}\n\n.dj-six {\n    grid-row: 4;\n    grid-column: 2;\n}\n\n/* --- Menu Page --- */\n#menu-card {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n}\n\n#menu-header {\n    font-family: \"Subheader-Main\";\n    font-size: 3rem;\n}\n\n#menu-card-two {\n    display: grid;\n    grid-template-rows: 1fr 1fr 1fr 1fr;\n    grid-template-columns: 1fr 1fr;\n    color: white;\n    padding: 0 3rem 40rem 3rem;\n    /* padding-bottom: 40rem; */\n    row-gap: 20px;\n    overflow: hidden;\n    align-items: center;\n}\n\n.menu-item-header {\n    font-size: 1.2rem;\n    text-align: center;\n}\n\n.menu-item-desc {\n    font-size: 0.6rem;\n}\n\n/* --- MEDIA QUERIES --- */\n@media (max-width: 450px){\n    .header__bottom > p {\n        font-size: 1rem;\n    }\n\n    .card__one > p {\n        font-size: 0.8rem;\n    }\n    \n    .card__two > p {\n        font-size: 0.8rem;\n    }\n\n    .footer {\n        display: grid;\n        grid-template-rows: 175px 125px 200px;\n        grid-template-columns: 1fr;\n        flex-direction: column;\n    }\n\n    .footer__contact {\n        grid-row: 1;\n        padding: 20px 40px 0 40px;\n    }\n\n    .footer__dresscode {\n        grid-row: 2;\n        grid-column: 1;\n    }\n\n    .footer__subscribe {\n        grid-row: 3;\n        grid-column: 1;\n        padding: 0 40px 0 40px;\n    }\n\n    .footer__input-div {\n        display: flex;\n        flex-direction: row;\n        align-items: center;\n    }\n\n    .footer__input-div > input {\n        width: 155px;\n        height: 15px;\n        padding: 15px;\n        margin-right: 5px;\n    }\n\n    .footer__input-div > button {\n        width: 100px;\n        height: 50px;\n    }\n\n    .footer__copyright {\n        grid-row: 4;\n    }\n\n    .menu-item-desc {\n        font-size: 0.5rem;\n    }\n}\n\n@media (max-width: 667px) {\n    .img-div {\n        height: 150px;\n        width: 150px;\n    }\n\n    .card__four {\n        padding: 15rem 20px 15rem 20px;\n    }\n}"],"sourceRoot":""}]);
